import React, { Component } from 'react';

import axios from 'axios';

import { ImagePicker, WingBlank, SegmentedControl } from 'antd-mobile';



const data = [];
class List extends Component {

    state = {
        files: data,
        multiple: false,
        information:"",
      }
      onChange = (files, type, index) => {
        console.log(files, type, index);
        console.log(files[0].url);
        let  tmpImage = files[0].url.split("data:image/jpeg;base64,") 
       
        var formData = new FormData();
        let msg = {'image':tmpImage[1],top_num:5,filter_threshold:0.95};
        for (const key in msg) {
            formData.append(key,msg[key])
          };
          let result ="";
          axios({
            method: 'POST',
            url: 'https://aip.baidubce.com/rest/2.0/image-classify/v2/dish?access_token=24.b19af59a8cc981ee0485ac7412983f71.2592000.1631180427.282335-24661909',
            data: formData
          }).then(res => {
            console.log(res);
          
            var p1 = Math.floor(Math.random() * 10) + 1;
            this.setState({
                information:res.data.result[0].name,
                price1:p1,
            })
          })

        this.setState({
          files,
        });
       
      }
      onSegChange = (e) => {
        const index = e.nativeEvent.selectedSegmentIndex;
        this.setState({
          multiple: index === 1,
        });
      }
  
  componentDidMount(){

    // axios.post('https://aip.baidubce.com/rest/2.0/image-classify/v2/dish?access_token=24.b19af59a8cc981ee0485ac7412983f71.2592000.1631180427.282335-24661909',{  
    //     params:{
    //         url:"https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fpic.baike.soso.com%2Fp%2F20140506%2F20140506100240-1827821352.jpg&refer=http%3A%2F%2Fpic.baike.soso.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1631188038&t=77330bb04de7581daec87fa47754005d",
    //         top_num: 5,
    //         filter_threshold:0.95
    //     }

       
    // },{headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
    //     .then(function (response) {
    //         console.log(response);
    //     })
    //     .catch(function (error) {
    //       console.log(error);
          
    //     })


  
  }

  render() {
    const { files,information ,price1} = this.state;
      return (
      <div>
          <h1></h1> 
           <ImagePicker
        files={files}
        onChange={this.onChange}
        onImageClick={(index, fs) => console.log(index, fs)}
        selectable={files.length < 3}
        multiple={this.state.multiple}
      />
        <span>{information} : {price1}</span>
        </div>
      )  
    
  }
}




export default List;


